@import 'animate.css';
@import '~loaders.css/src/animations/cube-transition.scss';

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.loader-active {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  width: 50px;
  height: 50px;
  animation: fadeOut 0.7s 0.7s;
  animation-fill-mode: forwards ;
}

@media screen and (max-width: 1200px) {
  .loader-active {
    display: block;
    position: absolute;
    left: 50px;
    right: 0;
    margin: auto;
    top: 50px;
    bottom: 0;
    width: 50px;
    height: 50px;
    animation: fadeOut 0.7s 0.7s;
    animation-fill-mode: forwards ;
  }
}