.page {
	width: 100%;
	height: 100%;
	position: absolute;
}

.top-tags {
	bottom: auto;
	top: 35px;
}

.tags {
	color: #ffd700;
	opacity: 0.6;
	position: absolute;
	bottom: 0;
	left: 120px;
	font-size: 18px;
	font-family: sans-serif;
}

.bottom-tag-html {
	margin-left: -20px;
}

.container {
	height: auto;
	width: 100%;
	will-change: contents;
	height: 90%;
	min-height: 566px;
	position: absolute;
	opacity: 0;
	top: 5%;
	margin: 0 auto;
	z-index: 1;
	transform-style: preserve-3d;
	animation: fadeIn 1s forwards;
	animation-delay: 1s;
}

.about-page,
.contact-page {
	.stack-text-zone {
		position: absolute;
		left: 50%;
		top: 37%;
		width: 50%;
		vertical-align: middle;
		display: table-cell;
		max-height: 90%;

		.ico-text-zone {
			position: relative;
			/* left: 50%; */
			padding-left: 5%;
			padding-right: 5%;
			margin-top: 10%;
			display: flex;
			align-items: center;
			justify-content: space-around;
			max-width: 80%;
			/* max-height: 90%; */
			justify-content: space-between;

			h1 {
				font-size: 53px;
				font-family: sans-serif;
				color: #ffd700;
				font-weight: 400;
				position: relative;
				left: 130px;
			}
		}

		.history-text-zone {
			position: relative;
			padding-left: 6%;
			padding-right: 4%;
			padding-top: 3%;
			padding-bottom: 5%;

			p {
				font-size: 14px;
				color: #fff;
				font-family: sans-serif;
				font-weight: 300;
				min-width: fit-content;
				animation: pulse 1s;
	
				&:nth-of-type(1) {
					animation-delay: 0s;
				}
			}
		}
	}

	.text-zone {
		position:absolute;
		left: 10%;
		top: 45%;
		transform: translateY(-50%);
		width: 35%;
		vertical-align: middle;
		display: table-cell;
		max-height: 90%;

		h1 {
			font-size: 53px;
			font-family: sans-serif;
			color: #ffd700;
			font-weight: 400;
			margin-top: 0;
			position: relative;
			margin-bottom: 40px;
			left: 10px;
		}

		p {
			font-size: 14px;
			color: #fff;
			font-family: sans-serif;
			font-weight: 300;
			min-width: fit-content;
			animation: pulse 1s;

			&:nth-of-type(1) {
				animation-delay: 1.1s;
			}

			&:nth-of-type(2) {
				animation-delay: 1.2s;
			}

			&:nth-of-type(3) {
				animation-delay: 1.3s;
			}
		}
	}
}

@media screen and (max-width: 1200px) {
	.page {
	  position: initial;
	}
  
	.container {
	  position: initial;
	  height: auto;
	  min-height: auto;
  
	  &.contact-page,
	  &.about-page,
	  &.portfolio-page {
		.text-zone {
		  position: initial;
		  transform: none;
		  width: 100%;
		  display: block;
		  padding-left: 3%;
		  padding-right: 4%;
		  padding-top: 3%;
		  padding-bottom: 5%;
		  box-sizing: border-box;
		}

		.stack-text-zone {
			position: absolute;
			left: 0;
			padding: 2%;
			top: 180%;
			width: 100%;
			display: flex;
			flex-direction: column;
			min-height: fit-content;
			max-height: max-content;
			height: auto;
	
			.ico-text-zone {
				position: relative;
				margin-top: 0;
				left: 2%;
				padding: 2%;
				top: 30%;
				display: flex;
				align-items: center;
				justify-content: space-around;
				width: 100%;
				/* max-height: 90%; */
				justify-content: space-between;
	
				h1 {
					font-size: 24px;
					font-family: sans-serif;
					color: #ffd700;
					font-weight: 400;
					position: relative;
					left: 15%;
					max-width: 95%;
					margin: 0;
				}
			}

			.history-text-zone {
				position: relative;
				display: flex;
				flex-direction: column;
				top: 140%;
				padding-left: 3%;
				padding-right: 4%;
				padding-top: 3%;
				padding-bottom: 5%;

				p {
					font-size: 13px;
					color: #fff;
					font-family: sans-serif;
					font-weight: 300;
					min-width: fit-content;
					animation: pulse 1s;
		
					&:nth-of-type(1) {
						animation-delay: 0.5s;
					}
				}
			}
		}
	
	  }
  
	  &.portfolio-page {
		width: 100%;
		padding: 20px;
		box-sizing: border-box;
  
		h1.page-title {
		  margin-left: 20px;
		  margin-top: 20px;
		}
  
		.image-box {
		  height: 200px;
		  max-width: calc(50% - 10px);
		}
	  }
	}
  
	.stage-cube-cont {
	  position: initial;
	  width: 100%;
	  height: 0%;
	  overflow: visible;
	}
  }

