.ico-face6{
    position: absolute;
    width: 80px;
    height: 80px;
    border: 1px solid #ccc;
    background: rgba(255, 255, 255, 0.4);
    text-align: center;
    font-size: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 20px 0px lightyellow;

    svg {
        width: 70%;
        height: 70%;
        position: absolute;
    }

    a {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 10;
      }
}