.indicate-cube-zone {
  position: relative;
  left: 50%;
  width: auto;

  p {
    font-family: sans-serif;
    font-size: 24px;
    font-weight: 300;
    color: #ffd700;
  }
}

.stage-cube-cont {
    width: 50%;
    height: 70%;
    top: 0;
    padding-top: 7%;
    margin-left: 0;
    position: absolute;
    right: -3%;
    overflow: hidden;
}
  
.cubespinner {
  animation-name: spincube;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-duration: 18s;
  transform-style: preserve-3d;
  transform-origin: 100px 100px 0;
  margin-left: calc(50% - 100px);

  div {
    position: absolute;
    width: 200px;
    height: 200px;
    border: 1px solid #ccc;
    background: rgba(255, 255, 255, 0.4);
    text-align: center;
    font-size: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 20px 0px lightyellow;
  }

  .face1 {
    transform: translateZ(100px);
    color: #dd0031;
  }
  .face2 {
    transform: rotateY(90deg) translateZ(100px);
    color: #f06529;
    
    a, svg {
      width: 100%;
      height: 100%;
      position: absolute;
    }

    a {
      z-index: 10;
    }
  }
  .face3 {
    transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
    color: #28a4d9;
  }
  .face4 {
    transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
    color: #5ed4f4;
    
    a, svg {
      width: 100%;
      height: 100%;
      position: absolute;
    }

    a {
      z-index: 10;
    }
  }
  .face5 {
    transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
    color: #efd81d;

    a, svg {
      width: 100%;
      height: 100%;
      position: absolute;
    }

    a {
      z-index: 10;
    }
  }
  .face6 {
    transform: rotateX(-90deg) translateZ(100px);
    color: #ec4d28;

    a {
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 10;
    }

    svg {
      width: 70%;
      height: 70%;
      position: absolute;
    }
  }
}
  
@keyframes spincube {
  from,
  to {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  16% {
    transform: rotateX(0deg) rotateY(-90deg) rotateZ(0deg);
  }
  33% {
    transform: rotateY(-90deg) rotateZ(90deg);
  }
  50% {
    transform: rotateY(-180deg) rotateZ(90deg);
  }
  66% {
    transform: rotateY(-270deg) rotateX(90deg);
  }
  83% {
    transform: rotateX(90deg);
  }
}

@media screen and (max-width: 1200px) {

  .indicate-cube-zone{
    left: 0;
    display: flex;
    justify-content: center;

    p {
      font-family: sans-serif;
      font-size: 18px;
      font-weight: 300;
      color: #ffd700;
    }
  }

  .stage-cube-cont {
    width: 100%;
    height: 100%;
    top: 90%;
    padding-top: 15%;
    margin-left: 0;
    position: absolute;
    left: 0;
    overflow: hidden;
  }

  .cubespinner {
